import React, { useEffect } from "react";
import { Link } from "react-router-dom";

import { Row, Col, Form, Input, Button, Alert } from "antd";
import { v4 as uuidv4 } from "uuid";
import { browserName } from "react-device-detect";
import { useHistory } from "react-router";
import { connect } from "react-redux";

import logo from "../../../../assets/images/logo/wheelsmart_logo_color.svg";

import LeftContent from "../leftContent";
// Redux
import { useDispatch } from "react-redux";

// actions
import {
  checkLogin,
  InputChangeValue,
  cleardata,
} from "../../../../store/auth/login/loginSlice";

function UserNameLogin(props) {
  const history = useHistory();
  const uuid = uuidv4();
  const [form] = Form.useForm();
  useEffect(() => {
    props.cleardata();
    // console.log("tokem", localStorage.getItem("access_token"));
    if (localStorage.getItem("access_token")) {
      history.push("/");
    }
  }, []);

  // useEffect(() => {
  //   return () => {
  //     if (history.action === "POP") {
  //       history.replace('/login');
  //     }
  //   };
  // }, [history]);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const onFinish = () => {
    var loginData = {
      password: props.loginData.password,
      username: props.loginData.username,
      device_uuid: uuid,
      device_name: browserName,
      device_type: 3, // web
    };
    props.checkLogin({
      loginData,
      history,
    });
  };

  console.log("props.loginData", props.loginData);

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  useEffect(() => {
    form.setFieldsValue({
      password: props.loginData.password,
      username: props.loginData.username,
    });
  }, [props.loginData]);
  return (
    <Row gutter={[32, 0]} className="hp-authentication-page">
      <LeftContent />

      <Col lg={12} span={24} className="hp-py-sm-0 hp-py-md-64">
        <Row className="hp-h-100" align="middle" justify="center">
          <Col>
            <div className="hp-m-0 hp-p-0">
              <img
                src={logo}
                style={{
                  height: 100,
                  width: 500,
                }}
                alt="Logo"
              />
            </div>
            <div
              xxl={11}
              xl={15}
              lg={20}
              md={20}
              sm={24}
              className="hp-px-sm-8 hp-pt-24 hp-pb-48"
            >
              <h1 className="hp-mb-sm-0">Login</h1>
              <p className="hp-mt-sm-0 hp-mt-8 hp-text-color-black-60">
                Welcome back, please login to your account.
              </p>

              <Form
                layout="vertical"
                name="loginWithUsername"
                className="hp-mt-sm-16 hp-mt-32"
                initialValues={{ remember: true }}
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
              >
                <Form.Item
                  label="User Name :"
                  name="username"
                  rules={[
                    {
                      required: true,
                      message: "Please input your User Name!",
                    },
                  ]}
                  hasFeedback
                >
                  <Input
                    id="username"
                    name="username"
                    style={{ width: "100%" }}
                    placeholder="User Name"
                    value={
                      props.loginData.username == null
                        ? ""
                        : props.loginData?.username
                    }
                    onChange={ChangeHandeler}
                  />
                </Form.Item>

                <Form.Item
                  label="Password :"
                  name="password"
                  rules={[
                    {
                      required: true,
                      message: "Please input your password!",
                    },
                  ]}
                  hasFeedback
                >
                  <Input.Password
                    id="password"
                    name="password"
                    style={{ width: "100%" }}
                    placeholder="Password"
                    value={
                      props.loginData?.password == null
                        ? ""
                        : props.loginData?.password
                    }
                    onChange={ChangeHandeler}
                  />
                </Form.Item>

                {/* {props.loginError && "hello "} */}
                {props.loginError?.password && (
                  <Alert message={props.loginError?.password} type="error" />
                )}
                {props.loginError?.detail && (
                  <Alert message={props.loginError?.detail} type="error" />
                )}
                {/* <Row align="middle" justify="space-between">
                <Form.Item className="hp-mb-0">
                  <Checkbox name="remember">Remember me</Checkbox>
                </Form.Item>
              </Row> */}
                <Form.Item className="hp-mt-16 hp-mb-8">
                  <Button block type="primary" htmlType="submit">
                    Sign in
                  </Button>
                </Form.Item>
              </Form>

              <div className="hp-form-info hp-mt-16">
                <Link
                  className="hp-text-color-primary-1 hp-text-color-dark-primary-2 hp-caption"
                  to="/mobilelogin"
                >
                  Login with Mobile
                </Link>
              </div>
            </div>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
function mapStateToProps({ login }) {
  return {
    loginError: login.loginError,
    loginData: login.loginData,
  };
}

export default connect(mapStateToProps, {
  checkLogin,
  InputChangeValue,
  cleardata,
})(UserNameLogin);
